import DaoProgressDirective from './dao-progress.directive.js';
import DaoProgressPulsingDirective from './dao-progress-pulsing.directive.js';
import DaoProgressStackedDirective from './dao-progress-stacked.directive.js';
const moduleName = 'dao.progress';

angular.module(moduleName, [])
  .directive('daoProgress', DaoProgressDirective)
  .directive('daoProgressPulsing', DaoProgressPulsingDirective)
  .directive('daoProgressStacked', DaoProgressStackedDirective)

export default moduleName;



