import template from './dao-copy-block.html';
import './dao-copy-block.scss';

function DaoCopyBlock() {
  'ngInject';

  return {
    restrict: 'E',
    scope: {
      content: '=',
    },
    template: template,
    transclude: true,
    replace: true
  };
}

export default DaoCopyBlock;



