import template from './dao-progress-stacked.html';
import './dao-progress.scss';
function DaoProgressStackedDirective() {
  'ngInject';

  return {
    restrict: 'E',
    template,
    scope: {
      green: '=',// green ∈ [0,1]
      red: '=',// red ∈ [0,1]
      stripe: '=',// stripe ∈ [0,1]
      black: '=',// grey ∈ [0,1]
    },
  };
}

export default DaoProgressStackedDirective;



