import template from './dao-input.html';
import templateWithIcon from './dao-input-with-icon.html';
import './dao-input.scss';
function DaoInputContainerDirective($sce, $timeout) {
  'ngInject';

  function linkFun($scope, element, attrs) {
    if (!attrs.hasOwnProperty('iconInside')) return;

    $scope.$watch('message', newV => {
      // 为解决 close 时，fade 过程中内容突然空掉的问题
      if (!newV) {
        $scope.showPopover = false;
        $timeout(() => {
          // 支持传入 html
          $scope._message = $sce.trustAsHtml(newV);
        }, 150);
      } else {
        $scope.showPopover = true;
        $scope._message = $sce.trustAsHtml(newV);
      }
    })
  }

  return {
    restrict: 'E',
    scope: {
      info: '=',
      loading: '=',
      error: '=',
      success: '=',
      warning: '=',
      message: '=',
      search: '@',
      messagePlacement: '@',
    },
    template: (el, attrs) => {
      if (attrs.hasOwnProperty('iconInside')) {
        return templateWithIcon;
      }
      return template;
    },
    link: linkFun,
    replace: true,
    transclude: true,
  };
}
export default DaoInputContainerDirective;



