import template from './dao-progress.html';
import './dao-progress.scss';
function DaoProgressDirective() {
  'ngInject';

  return {
    restrict: 'E',
    template,
    scope: {
      progress: '=',// progress ∈ [0,1]
      type: '=',// type ∈ ['usage']
    },
  };
}

export default DaoProgressDirective;



