import template from './dao-progress-pulsing.html';
import './dao-progress.scss';
function DaoProgressPulsingDirective() {
  'ngInject';

  return {
    restrict: 'E',
    template,
    scope: {
      progress: '=',// progress ∈ [0,1]
    },
  };
}

export default DaoProgressPulsingDirective;



