import template from './dao-select.html';

export default function SelectDirective() {

  function linkFun(scope, element, attrs, ngModel) {
    if(!ngModel) {
      return;
    }
    ngModel.$render = () => {

    }
    scope.set = (selected) => {
      ngModel.$setViewValue(selected);
    }
  }

  return {
    restrict: 'AE',
    replace: true,
    require: 'ngModel',
    transclude: true,
    scope: {
      ngModel: '=',
      withSearch: '@',
      withBtn: '@',
      withTab: '@',
      searchPlaceholder: '@',
      defaultTitle: '@',
      btnContent: '@',
      btnEvent: '=?',
      disabled: '=',
    },

    controller: SelectController,
    controllerAs: 'vm',
    bindToController:true,

    template: template,
    link: linkFun
  }
}

class SelectController {
  constructor($scope, $element, $document, $rootScope) {
    'ngInject';

    this.$scope = $scope;
    this.$element = $element;
    this.$document = $document;
    this.$rootScope = $rootScope;

    this.isOpen = false;
    this.filter = '';
    this.noOptions = false;

    this.$closefunc = this.closeChoices.bind(this);
    this.$options = [];
    this.$optionGroup = [];

    this.optionDOM = this.$element.children()[1];
    // console.log(this.optionDOM);

    //  兼容firefox
    this.optionDOM.addEventListener('DOMMouseScroll', (e) => {
      // console.log('firefox:',e);
      let withTab = angular.element(this.optionDOM).hasClass('withTab');
      if(withTab) {
        this.scrollDOM = angular.element(this.optionDOM)[0].querySelectorAll('.dao-tab-content')[0];
      }
      else{
        this.scrollDOM = angular.element(this.optionDOM)[0].querySelectorAll('.dao-select-items')[0];
      }

      // console.log(this.scrollDOM);

      // console.log(this.scrollDOM.clientHeight);
      // console.log(this.scrollDOM.scrollTop);
      // console.log(this.scrollDOM.scrollHeight);
      // console.log(e.detail);
      if(e.detail >= 0 && this.scrollDOM.clientHeight + this.scrollDOM.scrollTop == this.scrollDOM.scrollHeight) {
        // console.log('到底了');
        e.preventDefault();
      }
      if(e.detail < 0 && this.scrollDOM.scrollTop == 0) {
        // console.log('到头了');
        e.preventDefault();
      }
    });
    this.optionDOM.addEventListener('mousewheel', (e) => {
      // console.log('chrome:',e);
      let withTab = angular.element(this.optionDOM).hasClass('withTab');
      if(withTab) {
        this.scrollDOM = angular.element(this.optionDOM)[0].querySelectorAll('.dao-tab-content')[0];
      }
      else{
        this.scrollDOM = angular.element(this.optionDOM)[0].querySelectorAll('.dao-select-items')[0];
      }

      // console.log(this.scrollDOM);

      if(e.wheelDelta <= 0 && this.scrollDOM.clientHeight + this.scrollDOM.scrollTop == this.scrollDOM.scrollHeight) {
        // console.log('到底了');
        e.preventDefault();
      }
      if(e.wheelDelta > 0 && this.scrollDOM.scrollTop == 0) {
        // console.log('到头了');
        e.preventDefault();
      }

      // console.log(this.optionDOM);
      // console.log(this.scrollDOM.clientHeight);
      // console.log(this.scrollDOM.scrollTop);
      // console.log(this.scrollDOM.scrollHeight);

    });

    $scope.$watch('vm.ngModel', selected => {
      // console.log('selected变化了');
      if(selected == null){
        this.$options.forEach((option) => {
          option.isSelected = false;
        });
      }
      this.$options.forEach((option) => {
        if(option.value == selected){
          option.isSelected = true;
        }else{
          option.isSelected = false;
        }
      });
    });

    $scope.$watch('vm.filter', search => {
      // console.log(search);

      this.$options.forEach((option) => {
        // console.log(option);
        if(!search || option.text.indexOf(search) > -1 ){
          option.isShow = true;
        }else{
          option.isShow = false;
        }
      });

      for(let i = 0; i < this.$options.length; i++){

        if(this.$options[i].isShow == true){
          this.noOptions = false;
          break;
        }
        this.noOptions = true;

      }

      this.$optionGroup.forEach((optionGroup) => {
        for(let i = 0;i < optionGroup.vm.$options.length;i++){
          if(optionGroup.vm.$options[i].isShow == true){
            optionGroup.vm.showTip = false;
            break;
          }
          optionGroup.vm.showTip = true;
        }
      });
    });
  }

  deleteOption(optionScope) {
    let index = this.$options.indexOf(optionScope);
    // console.log(id);
    this.$options.splice(index,1);
  }
  addOption(optionScope) {
    this.$options.push(optionScope);
  }
  addOptionGroup(optionGroupScope){
    this.$optionGroup.push(optionGroupScope);
  }
  closeChoices(evt) {
    // console.log(evt);
    // console.log(this);
    let isClickInSelect = this.$element[0].contains(evt.target);
    // console.log(isClickInSelect);
    if(!isClickInSelect) {
      this.closeOptions();
    }
  }
  isEmpty() {
    if (this.ngModel == null) {
      return true;
    }
    else{
      return false;
    }
  }
  toggleOptions() {
    if(!!this.disabled){
      return;
    }

    //判断option-group是否有内容?
    this.$optionGroup.forEach((optionGroup) => {
      if(optionGroup.vm.$options.length == 0){
        optionGroup.vm.showTip = true;
      }else{
        optionGroup.vm.showTip = false;
      }
    });

    if(this.isOpen == false){
      this.isOpen = true;
      this.$document.bind('click', this.$closefunc);
    }else{
      this.closeOptions();
    }
  }
  setSelected(selected) {
    this.$options.forEach((option) => {
      // console.log(option);
      if(option.value == selected){
        option.isSelected = true;
      }else{
        option.isSelected = false;
      }
    });
    this.closeOptions();
    this.$scope.set(selected);
  }
  closeOptions() {
    this.isOpen = false;
    this.filter = '';
    this.$document.unbind('click', this.$closefunc);

    if(!this.$rootScope.$$phase) {
      this.$scope.$digest();
    }
  }
  getText() {
    for(let i = 0; i < this.$options.length; i++) {
      if(angular.equals(this.$options[i].value, this.ngModel)) {
        return this.$options[i].text;
      }
    }
  }
  getTab() {
    for(let i = 0; i < this.$options.length; i++) {
      if(angular.equals(this.$options[i].value, this.ngModel)) {
        return this.$options[i].tabName;
      }
    }
  }
}



