import DaoCopyBlock from './dao-copy-block.js';
import DaoClipboard from './clipboard/dao-clipboard.js';

const moduleName = 'dao.clipboard';
angular
  .module(moduleName, [])
  .directive('daoClipboard', DaoClipboard)
  .directive('daoCopyBlock', DaoCopyBlock);

export default moduleName;



