import template from './dao-option-group.html';

export default function OptionGroupDirective() {

  return {
    restrict: 'AE',
    replace: true,
    transclude: true,
    require: '^daoSelect',
    scope: {

      label: '@',
      noOptionTip: '@',
    },

    controller: OptionGroupController,
    controllerAs: 'vm',
    bindToController:true,
    template: template,
    link: linkFun,

  }
  function linkFun(scope, element, attrs, selectCtrl) {
    selectCtrl.addOptionGroup(scope);
    // if(!attrs.hasOwnProperty('noOptionTip')) {
    //   scope.vm.hasTip = false;
    // }
  }
}

class OptionGroupController {
  constructor($scope) {
    'ngInject';

    this.$options = [];
    this.$scope = $scope;
    this.showTip = false;

  }

  addOption(optionScope){
    this.$options.push(optionScope);
  }

  deleteOption(optionScope) {
    let index = this.$options.indexOf(optionScope);
    this.$options.splice(index,1);
  }
}



