import template from './editable-table.html';

function editableTableDirective() {
  return {
    restrict: 'E',
    scope: {
      data: '=',
      option: '=',
    },
    template,
    replace: true,
    controller: editableTableController,
    controllerAs: 'vm',
  }
}

class editableTableController {
  constructor($scope, $element, $attrs, $window) {
    'ngInject';

    this.$scope = $scope;
    this.$element = $element;
    this.$window = $window;

    this.data = $scope.data;
    this.option = $scope.option || {};
    this.data_clone = angular.copy(this.data, {});

    this.activate();
  }

  activate() {
    this.$scope.$watch('data', (newV, oldV) => {
      // 忽略因 controller 内部操作造成的数据变化
      if (angular.equals(this.data_clone, newV)) {
        return;
      }

      // 先自动取消
      if (typeof this.editIndex === 'number') {
        this.cancel();
      }

      // 只要外部的数据发生变化就更新列表内容
      this.data_clone = angular.copy(newV, {});
      // data 重新赋值了，地址都变了的情况
      this.data = newV;
    }, true);

    angular.element(window.document).on('click', e => {
      if (!angular.isNumber(this.editIndex)) return;
      const currentTr = this.$element.find('tbody').find('tr').eq(this.editIndex)[0];
      if (!this.isAncestor(currentTr, e.target)) {
        this.save();
        this.$scope.$apply();
      }
    });
  }

  isAncestor(ancestor, element) {
    let parent = element.parentNode;

    while (parent && parent !== ancestor) {
      parent = parent.parentNode;
    }

    return parent == ancestor;
  }

  // 点击编辑
  edit(rowIndex) {
    if (!this.checkError()) return;
    this.editIndex = rowIndex;
  }

  /**
   * This method is called
   * with composite ng-options:
   * [{
   *   label: '',
   *   value: ''
   * }]
   */
  updateCell(td) {
    for (let i = 0; i < td.options.length; i++) {
      if (td.options[i].value == td.value) {
        td.label = td.options[i].label;
      }
    }
  }

  // 开关按钮
  switch (rowIndex, colIndex) {
    let status = !!this.data_clone.body[rowIndex][colIndex].status;
    this.data_clone.body[rowIndex][colIndex].status = !status;
  }

  // 取消
  cancel() {
    angular.copy(this.data, this.data_clone);
    this.option.errorMessage = '';
    this.editIndex = null;
  }

  // 保存(点外面的时候保存)
  save() {
    if (!this.checkError()) return;
    angular.copy(this.data_clone, this.data);
    this.editIndex = null;
  }

  // 删除
  delete(rowIndex) {
    if (!this.checkError()) return;
    if (angular.isFunction(this.option.deleteRow)) {
      this.option.deleteRow(this.data_clone.body[rowIndex])
        .then(() => {
          this.data_clone.body.splice(rowIndex, 1);
          angular.copy(this.data_clone, this.data);
        })
    } else {
      this.data_clone.body.splice(rowIndex, 1);
      angular.copy(this.data_clone, this.data);
    }
  }

  // 添加一行
  add() {
    if (!this.checkError()) return;
    angular.merge(this.data_clone, this.data);
    if (this.data_clone.body.length > this.data.body.length) {
      angular.copy(this.data_clone, this.data);
    };
    const rowNum = this.data_clone.body.length;
    this.data_clone.body.push(angular.copy(this.option.addRow.body, []));
    this.editIndex = rowNum;
  }

  // 验证、显示错误信息
  checkError() {
    if (typeof this.editIndex !== 'number') return true;

    let correct = true;
    this.option.errorMessage = '';
    angular.forEach(this.data_clone.body[this.editIndex], v => {
      v.error = false;
      let value;
      v.value && (value = v.value.toString().trim());
      if (v.editable && !v.type && !v.options) {
        if (!value && v.required) {
          correct = false;
          v.error = true;
          this.option.errorMessage = '请填写完整';
          return;
        }
        if (v.required && v.reg && !v.reg.test(v.value)) {
          correct = false;
          v.error = true;
          this.option.errorMessage = '格式不正确';
          return;
        }
      }
    });

    if (!correct) {
      return correct;
    } else if (this.option.checkFun) {
      const errorMessage = this.option.checkFun(this.data_clone.body);
      if (errorMessage) {
        this.option.errorMessage = errorMessage;
        return false;
      }
    }

    return correct;
  }
}

export default editableTableDirective;



