import './dao-select.scss';

import SelectDirective from './dao-select';
import OptionDirective from './dao-option';
import OptionGroupDirective from './dao-option-group';


const moduleName = 'dao.select';
angular.module(moduleName, [])
  .directive('daoSelect', SelectDirective)
  .directive('daoOption', OptionDirective)
  .directive('daoOptionGroup', OptionGroupDirective)

export default moduleName;



