import template from './editable-dao-input.html';
import './editable-dao-input.scss';

function EditableDaoInputDirective() {
  'ngInject';

  return {
    restrict: 'E',
    template: template,
    scope: {
      data: '=',
      options: '='
    },
    replace: true,
    controller: editableDaoInputController,
    controllerAs: 'vm'
  };
}
class editableDaoInputController {
  constructor($scope, $element, $timeout) {
    'ngInject';

    this.$scope = $scope;
    this.$el = $element;
    this.$timeout = $timeout;

    this.init();
  }

  init() {
    this.options = this.$scope.options;
    // 是值引用不是地址引用，所以在下面需要 watch this.data，当 this.data 值改变时需要同步到 this.$scope.data
    this.data = this.$scope.data;
    this.dataOrigin = this.$scope.data;
    this.isEdit = this.options.editState;
    this.isError = false;
    this.isFocus = this.isEdit;
    this.activate();
  }

  activate() {
    this.$scope.$watch(() => this.data, newVal => this.setInputData(newVal));
    this.$scope.$watch(() => this.$scope.data, newVal => this.setOriginData(newVal));
  }

  setInputData(val) {
    // 为了将值传出去，用于外部 checkFun 的回调方法里面需要用到 this.$scope.data
    this.$scope.data = val;
    if (val === this.dataOrigin) {
      // 值被还原的时候不检测
      this.isError = false;
      return;
    }
    // 需要值传出去并且脏检查之后，才能执行 checkFun
    // 作用：实时检测
    this.$timeout(() => {
      this.isError = !this.options.checkFun();
    });
  }

  setOriginData(val) {
    // 当 this.data !== this.$scope.data 时 this.$scope.data 是外部同步进来的
    // 为了确定值是外部同步进来的还是内部修改的，需要赋值到 dataOrigin 变量，所以需要 watch this.data 以及 this.$scope.data
    if (this.data !== val) {
      this.dataOrigin = val;
      // 为了将值同步进来
      this.data = val;
    }
  }

  cancel() {
    this.isEdit = false;
    this.isError = false;
    this.data = this.dataOrigin;
  }

  edit() {
    this.isEdit = true;
    // 不在 disabled 状态时，才能设置焦点，所以要用到 $timeout
    this.$timeout(() => {
      angular.element(this.$el[0]).find('input')[0].focus();
    });
  }

  // ng-keyPress 不能监听 esc 键，所以用 keyUp，Firefox 在 keydown 下也不能监听 esc 键
  keyUp(e) {
    switch(e.keyCode) {
      case 13: // enter 键
        this.save();
        break;
      case 27: // esc 键
        this.cancel();
        break;
    }
  }

  save() {
    if (this.options.checkFun()) {
      this.dataOrigin = this.data;
      this.options.successFun();
      this.isEdit = false;
      this.isError = false;
    } else {
      this.isError = true;
      angular.element(this.$el[0]).find('input')[0].focus();
    }
  }
}

export default EditableDaoInputDirective;



