import DaoInputContainerDirective from './dao-input.directive';
import editableInput from './editable-dao-input.directive';
const moduleName = 'dao.input';

angular
  .module(moduleName, [])
  .directive('daoInputContainer', DaoInputContainerDirective)
  .directive('editableDaoInput', editableInput);

export default moduleName;



